.headerPage {
  background-color: #d4e2d4;
  position: relative;
  bottom: 72px;
  margin-bottom: 50px;
}

h1 {
  text-align: center;
  padding-top: 50px;
}

.homeButton {
  border-radius: 5px;
  margin-left: 23%;
  border: 0ch;
  margin-bottom: 20px;
  padding: 20px;
}

.cartButton {
  border-radius: 5px;
  margin-left: 47%;
  border: 0ch;
  padding: 15px;
  height: 80px;
  position: relative;
  top: 10px;
}

.cartButton ion-icon {
  vertical-align: bottom;
  font-size: 40px;
  cursor: pointer;
}

.homeButton ion-icon {
  vertical-align: bottom;
  font-size: 40px;
  cursor: pointer;
}

.homeBody {
  background-color: #fcf8e8;
  height: 2000px;
  padding-top: 50px;
}

.products {
  position: relative;
  height: 500px;
  width: 300px;
  background-color: rgba(211, 211, 211, 0.432);
  border-radius: 20px;
  text-align: center;
  align-items: center;
  margin-left: 10%;
  margin-top: 30px;
}

.products img {
  width: 70%;
  margin-top: 30px;
  border-radius: 20px;
}

.image img {
  width: 100px;
}

h3 {
  width: 300px;
}
